import axios from 'axios';
import React, {useEffect, useState} from 'react';

import {text} from '../text';
import {URLS} from '../config';
import {items} from '../items';
import {theme} from '../constants';
import {PromocodeType} from '../types';
import {components} from '../components';

const categories = ['current', 'used'];

export const MyPromocodes: React.FC = () => {
  const [loading, setLoading] = useState<boolean>(false);
  const [promocodesData, setPromocodesData] = useState<PromocodeType[]>([]);
  const [selectedCategory, setSelectedCategory] = useState<string>(
    categories[0]
  );

  const getData = async () => {
    setLoading(true);

    try {
      const promocodes = await axios
        .get(URLS.GET_PROMOCODES)
        .then((res) => res.data.promocodes);

      setPromocodesData(promocodes);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getData();
    window.scrollTo(0, 0);
  }, []);

  const renderHeader = (): JSX.Element => {
    return <components.Header goBack={true} title='My promocodes' />;
  };

  const renderGroup = (): JSX.Element => {
    return (
      <div
        style={{
          gap: 8,
          paddingBottom: 20,
          alignSelf: 'center',
          alignItems: 'center',
          flexDirection: 'row',
          justifyContent: 'center',
          width: '100%',
          marginBottom: 20,
          borderBottom: `1px solid ${theme.colors.lavenderMist}`,
        }}
      >
        {categories.map((item, index) => {
          return (
            <span
              key={index}
              style={{
                borderRadius: 30,
                padding: '1px 20px 3px 20px',
                backgroundColor:
                  selectedCategory === item
                    ? theme.colors.mainColor
                    : theme.colors.lavenderMist,
                cursor: 'pointer',
              }}
              onClick={() => setSelectedCategory(item)}
            >
              <text.T14
                style={{
                  color:
                    selectedCategory === item
                      ? theme.colors.white
                      : theme.colors.black,
                  textTransform: 'capitalize',
                }}
              >
                {item}
              </text.T14>
            </span>
          );
        })}
      </div>
    );
  };

  const renderPromocodes = (): JSX.Element => {
    return (
      <>
        {promocodesData?.map((item, index, array) => {
          const isLast = index === array.length - 1;
          return (
            <items.PromocodeItem key={index} item={item} isLast={isLast} />
          );
        })}
      </>
    );
  };

  const renderContent = (): JSX.Element => {
    if (loading) return <components.Loader />;

    return (
      <main style={{padding: '82px 0 20px 0'}}>
        {renderGroup()}
        {renderPromocodes()}
      </main>
    );
  };

  return (
    <div id='screen'>
      {renderHeader()}
      {renderContent()}
    </div>
  );
};
