import {useState, useEffect} from 'react';
import {useNavigate} from 'react-router-dom';

import {text} from '../text';
import {utils} from '../utils';
import {svg} from '../assets/svg';
import {theme} from '../constants';
import {hooks, RootState} from '../hooks';
// import {RootState} from '../store';
// import {actions} from '../store/actions';

type Props = {
  logo?: string;
  title?: string;
  line?: boolean;
  burger?: boolean;
  goBack?: boolean;
  basket?: boolean;
};

export const Header: React.FC<Props> = ({
  logo,
  line,
  title,
  burger,
  goBack,
  basket,
}) => {
  const navigate = useNavigate();
  const [modal, setModal] = useState(false);

  // const dispatch = hooks.useAppDispatch();

  useEffect(() => {
    if (modal) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = '';
    }

    return () => {
      document.body.style.overflow = '';
    };
    // document.body.style.overflow = 'hidden';
  }, [modal]);

  const {list, total} = hooks.useAppSelector(
    (state: RootState) => state.cartSlice
  );

  // const handleOnClick = () => {
  //   if (cart.length > 0) {
  //     dispatch(actions.setScreen('Order'));
  //     navigate('/TabNavigator');
  //     return;
  //   }

  //   return alert('Your cart is empty');
  // };

  const renderGoBack = (): JSX.Element | null => {
    if (goBack) {
      return (
        <button
          style={{position: 'absolute', left: 0, padding: 20}}
          onClick={() => navigate(-1)}
        >
          <svg.GoBackSvg />
        </button>
      );
    }

    return null;
  };

  const renderBurger = (): JSX.Element | null => {
    if (burger) {
      return (
        <button
          style={{
            position: 'absolute',
            left: 0,
            padding: 20,
            cursor: 'pointer',
            backgroundColor: 'transparent',
            border: 'none',
          }}
          onClick={() => setModal(true)}
        >
          <svg.BurgerSvg />
        </button>
      );
    }

    return null;
  };

  const renderTitle = (): JSX.Element => {
    return (
      <text.H4
        style={{
          left: '50%',
          textAlign: 'center',
          position: 'absolute',
          width: 'calc(100% - 100px)',
          transform: 'translateX(-50%)',
        }}
      >
        {title}
      </text.H4>
    );
  };

  const renderBasket = (): JSX.Element | null => {
    if (basket) {
      return (
        <button
          // onClick={handleOnClick}
          style={{
            position: 'absolute',
            right: 0,
            paddingTop: 12,
            paddingBottom: 12,
            paddingLeft: 20,
            paddingRight: 20,
            borderRadius: 12,
          }}
        >
          <svg.BasketSvg />
          <span
            style={{
              padding: '3px 6px 3px 6px',
              borderRadius: 30,
              right: 34,
              bottom: 12,
              position: 'absolute',
              fontSize: 10,
              color: theme.colors.white,
              ...theme.fonts.Mulish_700Bold,
              backgroundColor: theme.colors.accentColor,
            }}
          >
            {list.length > 0 ? `$${total.toFixed(2)}` : '$0'}
          </span>
        </button>
      );
    }

    return null;
  };

  const renderLogo = (): JSX.Element | null => {
    if (logo) {
      return (
        <svg.LogoSvg
          style={{
            marginBottom: 8,
          }}
        />
      );
    }

    return null;
  };

  const headerStyle: React.CSSProperties = {
    // top: 0,
    height: 52,
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
    width: '100%',
    top: 0,
    position: 'fixed',
    maxWidth: 650,
    // border: '2px solid blue',
    zIndex: 1,
    // ...utils.rowCenterSpcBtw(),
    backgroundColor: theme.colors.white,
    borderBottom: line ? `1px solid ${theme.colors.aliceBlue2}` : 'none',
  };

  return (
    <header style={{...headerStyle}}>
      {renderGoBack()}
      {renderBurger()}
      {renderTitle()}
      {renderLogo()}
      {renderBasket()}
    </header>
  );
};
