import axios from 'axios';
import React, {useEffect, useState} from 'react';
import useEmblaCarousel from 'embla-carousel-react';

import {utils} from '../../utils';
import {URLS} from '../../config';
import {items} from '../../items';
import {hooks} from '../../hooks';
import {custom} from '../../custom';
import {theme} from '../../constants';
import {actions} from '../../store/actions';
import {components} from '../../components';
import {BannerType, ProductType, CarouselType} from '../../types';

export const Home: React.FC = () => {
  const dispatch = hooks.useAppDispatch();
  const navigate = hooks.useAppNavigate();

  const [emblaRef, emblaApi] = useEmblaCarousel();
  const [activeIndex, setActiveIndex] = useState(0);
  const [loading, setLoading] = useState<boolean>(true);
  const [bannersData, setBannersData] = useState<BannerType[]>([]);
  const [carouselData, setCarouselData] = useState<CarouselType[]>([]);
  const [productsData, setProductsData] = useState<ProductType[]>([]);

  console.log('productsData --->', JSON.stringify(productsData, null, 2));

  const handleSlideChange = () => {
    if (emblaApi) {
      setActiveIndex(emblaApi.selectedScrollSnap());
    }
  };

  useEffect(() => {
    if (emblaApi) {
      emblaApi.on('select', handleSlideChange);
    }
  }, [emblaApi]);

  const getData = async () => {
    setLoading(true);

    try {
      const products = await axios
        .get(URLS.GET_PRODUCTS)
        .then((res) => res.data.products);

      const categories = await axios
        .get(URLS.GET_CATEGORIES)
        .then((res) => res.data.categories);

      const banners = await axios
        .get(URLS.GET_BANNERS)
        .then((res) => res.data.banners);

      const carousel = await axios
        .get(URLS.GET_CAROUSEL)
        .then((res) => res.data.carousel);

      setBannersData(banners);
      setProductsData(products);
      setCarouselData(carousel);
      // setCategoriesData(categories);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getData();
    window.scrollTo(0, 0);
  }, []);

  const renderHeader = (): JSX.Element => {
    return <components.Header burger={true} basket={true} line={true} />;
  };

  const renderCarouselContent = (): JSX.Element => {
    return (
      <div className='embla' ref={emblaRef} style={{marginBottom: 20}}>
        <div className='embla__container'>
          {carouselData.map((item: CarouselType) => {
            return (
              <items.CarouselItem
                item={item}
                key={item.id}
                productsData={productsData}
              />
            );
          })}
        </div>
      </div>
    );
  };

  const renderIndicator = (): JSX.Element => {
    return (
      <div style={{gap: 5, ...utils.rowCenter(), justifyContent: 'center'}}>
        {carouselData?.map((item: CarouselType, index: number) => {
          const isSelected = index === activeIndex;
          return <items.DotItem key={item.id} isSelected={isSelected} />;
        })}
      </div>
    );
  };

  const renderCarousel = () => {
    return (
      <section style={{position: 'relative', marginBottom: 40}}>
        {renderCarouselContent()}
        {renderIndicator()}
      </section>
    );
  };

  const renderBestSellers = (): JSX.Element | null => {
    if (!productsData.length) return null;
    return (
      <section
        style={{marginBottom: 40, display: 'flex', flexDirection: 'column'}}
      >
        <components.BlockHeading
          title='Best sellers'
          containerStyle={{padding: '0 20px', marginBottom: 14}}
          viewAllOnClick={() => {
            dispatch(actions.resetFilters());
            navigate('/Shop', {
              state: {products: productsData, title: 'Best sellers'},
            });
          }}
        />
        <custom.ScrollView style={{paddingLeft: 20, paddingRight: 20}}>
          {productsData?.map(
            (item: ProductType, index: number, arra: ProductType[]) => {
              const isLast = index === arra.length - 1;
              return (
                <items.ProductCard
                  key={item.id}
                  isLast={isLast}
                  item={item}
                  version={1}
                />
              );
            }
          )}
        </custom.ScrollView>
      </section>
    );
  };

  const renderBanner = (): JSX.Element => {
    return (
      <div style={{marginBottom: 40}}>
        <img
          src={bannersData[0]?.image}
          alt='Banner'
          style={{width: '100%', cursor: 'pointer'}}
          onClick={() => {
            dispatch(actions.resetFilters());
            navigate('/Shop', {state: {products: productsData}});
          }}
        />
      </div>
    );
  };

  const renderContent = (): JSX.Element => {
    if (loading) return <components.TabLoader />;
    return (
      <main
        style={{
          paddingBottom: 64,
          display: 'flex',
          flexDirection: 'column',
          marginTop: 52,
        }}
      >
        {renderCarousel()}
        {renderBestSellers()}
        {renderBanner()}
      </main>
    );
  };

  const renderBottomTabBar = () => {
    return <components.BottomTabBar />;
  };

  return (
    <>
      {renderHeader()}
      {renderContent()}
      {renderBottomTabBar()}
    </>
  );
};
