import React, {useEffect} from 'react';

import {text} from '../text';
import {utils} from '../utils';
import {hooks} from '../hooks';
import {svg} from '../assets/svg';
import {theme} from '../constants';
import {components} from '../components';

export const Checkout: React.FC = () => {
  const navigate = hooks.useAppNavigate();

  const cart = hooks.useAppSelector((state) => state.cartSlice.list);
  const total = hooks.useAppSelector((state) => state.cartSlice.total);
  const discount = hooks.useAppSelector((state) => state.cartSlice.discount);
  const delivery = hooks.useAppSelector((state) => state.cartSlice.delivery);

  const textStyle = {
    margin: 0,
    fontSize: 14,
    lineHeight: 1.5,
    fontWeight: 400,
    color: theme.colors.textColor,
    ...theme.fonts.Mulish_400Regular,
  };

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const renderHeader = (): JSX.Element => {
    return <components.Header title='Checkout' goBack={true} line={true} />;
  };

  const renderMyOrder = (): JSX.Element => {
    return (
      <div style={{marginBottom: 20}}>
        <div
          style={{
            marginBottom: 8,
            padding: '0 20px 6px 20px',
            ...utils.rowCenterSpcBtw(),
          }}
        >
          <text.H4 style={{color: theme.colors.mainColor}}>My order</text.H4>
          <text.H4 style={{color: theme.colors.mainColor}}>
            ${total.toFixed(2)}
          </text.H4>
        </div>
        <div
          style={{
            padding: 20,
            borderTop: '4px solid #EDF1FA',
            borderBottom: '4px solid #EDF1FA',
            backgroundColor: `${theme.colors.lavenderMist}30`,
          }}
        >
          {/* PRODUCTS */}
          {cart.map((item, index) => {
            return (
              <div
                key={index}
                style={{marginBottom: 10, ...utils.rowCenterSpcBtw()}}
              >
                <text.T14 style={{textTransform: 'capitalize'}}>
                  {item.name}
                  {item.size && `, ${item.size}`}
                  {item.color && `, ${item.color}`}
                </text.T14>
                <text.T14 style={{textTransform: 'capitalize'}}>
                  {item.quantity} x ${item.price}
                </text.T14>
              </div>
            );
          })}
          {/* TOTAL */}
          {discount > 0 && (
            <div style={{...utils.rowCenterSpcBtw(), marginBottom: 10}}>
              <text.T14 style={{textTransform: 'capitalize'}}>
                Discount
              </text.T14>
              <text.T14 style={{textTransform: 'capitalize'}}>
                - {discount}
              </text.T14>
            </div>
          )}
          {/* DELIVERY */}
          <div style={{...utils.rowCenterSpcBtw()}}>
            <text.T14 style={{textTransform: 'capitalize'}}>Delivery</text.T14>
            <text.T14 style={{textTransform: 'capitalize', color: '#51BA74'}}>
              {`${delivery === 0 ? 'Free' : `$${delivery.toFixed(2)}`}`}
            </text.T14>
          </div>
        </div>
      </div>
    );
  };

  const renderShippingDetails = (): JSX.Element => {
    return (
      <div
        style={{
          marginBottom: 20,
          padding: '0 20px',
          cursor: 'pointer',
          ...utils.rowCenterSpcBtw(),
          borderBottom: '4px solid #EDF1FA',
        }}
        onClick={() => navigate('/CheckoutShippingDetails')}
      >
        <div>
          <text.H4 style={{marginBottom: 10, color: theme.colors.mainColor}}>
            Shipping details
          </text.H4>
          <text.T14 style={{...textStyle, marginBottom: 10}}>
            8000 S Kirkland Ave, Chicago, IL 6065...
          </text.T14>
        </div>
        <svg.RightArrowSvg style={{marginBottom: 14}} />
      </div>
    );
  };

  const renderPaymentMethod = (): JSX.Element => {
    return (
      <div
        style={{
          marginBottom: 40,
          cursor: 'pointer',
          padding: '0 20px',
          ...utils.rowCenterSpcBtw(),
          borderBottom: '4px solid #EDF1FA',
        }}
        onClick={() => navigate('/CheckoutPaymentMethod')}
      >
        <div>
          <text.H4 style={{marginBottom: 10, color: theme.colors.mainColor}}>
            Payment method
          </text.H4>
          <text.T14 style={{...textStyle, marginBottom: 10}}>
            7741 ******** 6644
          </text.T14>
        </div>
        <svg.RightArrowSvg style={{marginBottom: 14}} />
      </div>
    );
  };

  const renderButton = (): JSX.Element => {
    return (
      <components.Button
        title='Confirm order'
        onClick={() => {
          // navigate('/OrderSuccessful');
          navigate('/OrderFailed');
        }}
        containerStyle={{
          padding: '0 20px',
        }}
      />
    );
  };

  const renderContent = (): JSX.Element => {
    return (
      <main style={{padding: '82px 0 0 0'}}>
        {renderMyOrder()}
        {renderShippingDetails()}
        {renderPaymentMethod()}
        {renderButton()}
      </main>
    );
  };

  return (
    <div id='screen'>
      {renderHeader()}
      {renderContent()}
    </div>
  );
};
