import React from 'react';

import {utils} from '../utils';
import {hooks} from '../hooks';
import {custom} from '../custom';
import {components} from '../components';

export const AddANewCard: React.FC = () => {
  const navigate = hooks.useAppNavigate();

  const renderHeader = (): JSX.Element => {
    return <components.Header title='Add a new card' goBack={true} />;
  };

  const renderContent = (): JSX.Element => {
    return (
      <main style={{padding: '75px 20px 0 20px'}}>
        <components.Line style={{marginBottom: 14}} />
        <img
          src={require('../assets/other/04.png')}
          alt='Card'
          style={{width: '80%', alignSelf: 'center', marginBottom: 40}}
        />
        <custom.InputField
          label='Name'
          placeholder='Kristin Watson'
          containerStyle={{marginBottom: 20}}
        />
        <custom.InputField
          label='card number'
          placeholder='7741 6588 2123 6644'
          containerStyle={{marginBottom: 20}}
        />
        <div style={{...utils.rowCenterSpcBtw()}}>
          <custom.InputField
            label='Expiry date'
            placeholder='MM/YY'
            containerStyle={{marginBottom: 20, width: '48%'}}
          />
          <custom.InputField
            label='CVV'
            placeholder='123'
            containerStyle={{marginBottom: 20, width: '48%'}}
          />
        </div>
        <components.Button
          title='save card'
          onClick={() => {
            navigate(-1);
          }}
        />
      </main>
    );
  };

  return (
    <div id='screen'>
      {renderHeader()}
      {renderContent()}
    </div>
  );
};
