import React from 'react';

import {text} from '../text';
import {hooks} from '../hooks';
import {custom} from '../custom';
import {product} from '../product';
import {theme} from '../constants';
import {ProductType} from '../types';

type Props = {item: ProductType; isLast: boolean};

export const OrderItem: React.FC<Props> = ({item, isLast}) => {
  const navigate = hooks.useAppNavigate();

  const renderImage = (): JSX.Element => {
    return (
      <button onClick={() => navigate('/product', {state: {item}})}>
        <custom.ImageBackground
          imageUrl={item.images[0]}
          style={{
            width: 100,
            height: '100%',
            backgroundColor: theme.colors.imageBackground,
            objectFit: 'cover',
          }}
        >
          <product.ProductSale
            item={item}
            style={{position: 'absolute', top: 0, left: 0}}
          />
        </custom.ImageBackground>
      </button>
    );
  };

  const renderInfo = (): JSX.Element => {
    return (
      <div
        style={{
          borderColor: theme.colors.antiFlashWhite,
          width: '100%',
          paddingRight: 0,
          flexDirection: 'row',
          flex: 1,
          display: 'flex',
          marginLeft: 14,
        }}
      >
        <div
          style={{
            display: 'flex',
            paddingRight: 14,
            marginRight: 'auto',
            flexDirection: 'column',
          }}
        >
          <product.ProductName item={item} numberOfLines={1} />
          <product.ProductPrice
            item={item}
            containerStyle={{marginBottom: 'auto'}}
          />
          <text.T14>Size: {item.size ? item.size : 'No size'}</text.T14>
          <text.T14>Color: {item.color ? item.color : 'No color'}</text.T14>
        </div>
        <product.ProductCounter item={item} />
      </div>
    );
  };

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'row',
        height: 100,
        padding: '0 0 0 20px',
        marginBottom: isLast ? 20 : 14,
      }}
    >
      {renderImage()}
      {renderInfo()}
    </div>
  );
};
