import React from 'react';

import {text} from '../text';
import {hooks} from '../hooks';
import {svg} from '../assets/svg';
import {components} from '../components';

export const OrderFailed: React.FC = () => {
  const navigate = hooks.useAppNavigate();

  const renderContent = (): JSX.Element => {
    return (
      <main style={{padding: '0 20px', marginTop: '20%'}}>
        <svg.FailSvg style={{alignSelf: 'center', marginBottom: 20}} />
        <components.Line style={{marginBottom: 14}} />
        <text.H2 style={{textAlign: 'center', marginBottom: 14}}>
          Sorry! Your order has failed!
        </text.H2>
        <text.T16
          style={{
            textAlign: 'center',
            whiteSpace: 'pre-line',
            marginBottom: 30,
          }}
        >
          Something went wrong. Please try{'\n'}again to contimue your order.
        </text.T16>
        <components.Button
          title='try again'
          onClick={() => {}}
          containerStyle={{marginBottom: 10}}
        />
        <components.Button
          title='go to my profile'
          onClick={() => {
            navigate('/TabNavigator');
          }}
          colorScheme='transparent'
        />
      </main>
    );
  };

  return <div id='screen'>{renderContent()}</div>;
};
