import axios from 'axios';
import React, {useEffect, useState} from 'react';

import {text} from '../../text';
import {hooks} from '../../hooks';
import {items} from '../../items';
import {URLS} from '../../config';
import {custom} from '../../custom';
import {svg} from '../../assets/svg';
import {theme} from '../../constants';
import {components} from '../../components';

export const Profile: React.FC = () => {
  const navigate = hooks.useAppNavigate();

  const [user, setUser] = useState<any>(null);
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    getData();
    window.scrollTo(0, 0);
  }, []);

  const getData = async () => {
    setLoading(true);

    try {
      const user = await axios.get(URLS.GET_USER).then((res) => res.data.user);

      setUser(user);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const renderHeader = (): JSX.Element => {
    return <components.Header burger={true} basket={true} line={true} />;
  };

  const renderUserDetails = (): JSX.Element => {
    return (
      <div style={{marginBottom: 22, alignSelf: 'center'}}>
        <div
          style={{
            height: 120,
            width: 120,
            borderRadius: '50%',
            alignSelf: 'center',
            marginBottom: 20,
            position: 'relative',
            cursor: 'pointer',
          }}
          onClick={() => {
            navigate('/EditProfile');
          }}
        >
          <img
            src={user?.photo}
            style={{
              height: '100%',
              width: '100%',
              borderRadius: '50%',
              border: `6px solid ${theme.colors.lavenderMist}`,
            }}
          />
          <svg.EditSvg style={{position: 'absolute', bottom: 0, right: 0}} />
        </div>
        <text.H3 style={{textAlign: 'center', marginBottom: 4}}>
          Kristin Watson
        </text.H3>
        <text.T14 style={{textAlign: 'center'}}>
          kristinwatson@mail.com
        </text.T14>
      </div>
    );
  };

  const renderMenu = (): JSX.Element => {
    return (
      <custom.View>
        <items.ProfileItem
          title='Order history'
          onClick={() => {
            navigate('/OrderHistory');
          }}
          icon={<svg.CalendarSvg />}
        />
        <items.ProfileItem
          title='Payment method'
          icon={<svg.CreditCardSvg />}
          onClick={() => {
            navigate('/PaymentMethod');
          }}
        />
        <items.ProfileItem
          title='My address'
          icon={<svg.MapPinSvg />}
          onClick={() => {
            navigate('/MyAddress');
          }}
        />
        <items.ProfileItem
          title='My promocodes'
          icon={<svg.GiftSvg />}
          onClick={() => {
            // navigate('/MyPromocodes');
            navigate('/MyPromocodesEmpty');
          }}
        />
        <items.ProfileItem
          title='Sign out'
          navIcon={false}
          icon={<svg.LogOutSvg />}
          onClick={() => {
            navigate('/');
          }}
        />
      </custom.View>
    );
  };

  const renderContent = (): JSX.Element => {
    if (loading) return <components.TabLoader />;

    return (
      <main style={{paddingTop: 40, paddingBottom: 64 + 30, marginTop: 52}}>
        {renderUserDetails()}
        {renderMenu()}
      </main>
    );
  };

  const renderBottomTabBar = () => {
    return <components.BottomTabBar />;
  };

  return (
    <div id='screen'>
      {renderHeader()}
      {renderContent()}
      {renderBottomTabBar()}
    </div>
  );
};
