import React, {useEffect} from 'react';

import {hooks} from '../hooks';
import {screens} from '../screens';
import {RootState} from '../store';

export const TabNavigator: React.FC = () => {
  const screen = hooks.useAppSelector(
    (state: RootState) => state.tabSlice.screen
  );

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [screen]);

  // console.log('productsData --->', JSON.stringify(productsData, null, 2));

  const cart = hooks.useAppSelector((state: RootState) => state.cartSlice.list);
  const wishlist = hooks.useAppSelector(
    (state: RootState) => state.wishlistSlice.list
  );

  const renderScreen = (): JSX.Element => {
    return (
      <div className='full-height'>
        {screen === 'Home' && <screens.Home />}
        {screen === 'Search' && <screens.Categories />}
        {screen === 'Order' && cart.length > 0 && <screens.Order />}
        {screen === 'Order' && !cart.length && <screens.CartEmpty />}
        {screen === 'Wishlist' && wishlist.length > 0 && <screens.Wishlist />}
        {screen === 'Wishlist' && !wishlist.length && <screens.WishlistEmpty />}
        {screen === 'Profile' && <screens.Profile />}
      </div>
    );
  };

  return renderScreen();
};
