import React from 'react';

import {utils} from '../utils';
import {hooks} from '../hooks';
import {custom} from '../custom';
import {theme} from '../constants';
import {product} from '../product';
import {ProductType} from '../types';

type Props = {item: ProductType; containerStyle?: any; isLast?: boolean};

export const WishlistItem: React.FC<Props> = ({
  item,
  isLast,
  containerStyle,
}): JSX.Element => {
  const navigate = hooks.useAppNavigate();

  const renderImage = (): JSX.Element => {
    return (
      <button
        onClick={() => {
          navigate('/product', {state: {item}});
        }}
      >
        <custom.ImageBackground
          imageUrl={item.images[0]}
          style={{
            width: 100,
            height: 100,
            backgroundColor: theme.colors.imageBackground,
          }}
        >
          {item.oldPrice && (
            <product.ProductSale
              item={item}
              style={{position: 'absolute', top: 0, left: 0}}
            />
          )}
        </custom.ImageBackground>
      </button>
    );
  };

  const renderDetails = (): JSX.Element => {
    return (
      <div style={{flex: 1, ...utils.rowSpcBtw()}}>
        {/* Left content */}
        <custom.View style={{padding: '14px 0 10px 14px'}}>
          <product.ProductName item={item} />
          <product.ProductPrice
            item={item}
            containerStyle={{marginBottom: 9}}
          />
          <product.ProductRating rating={item.rating} ratingCount={3} />
        </custom.View>
        {/* Right content */}
        <div style={{...utils.columnCenterSpcBtween()}}>
          <product.ProductInWishlist
            item={item}
            version={1}
            style={{padding: '0 20px'}}
          />
          <product.ProductInCart
            item={item}
            version={1}
            style={{padding: '0 20px'}}
          />
        </div>
      </div>
    );
  };

  return (
    <div
      style={{
        ...containerStyle,
        display: 'flex',
        flexDirection: 'row',
        paddingLeft: 20,
        paddingBottom: 20,
        width: '100%',
        marginBottom: isLast ? 0 : 20,
        borderBottom: isLast ? 'none' : '1px solid #DBE3F5',
        // backgroundColor: 'red',
      }}
    >
      {renderImage()}
      {renderDetails()}
    </div>
  );
};
