import React from 'react';

import {hooks} from '../hooks';
import {theme} from '../constants';
import {ProductType} from '../types';
import {actions} from '../store/actions';

type Props = {item: ProductType};

const renderMinusSvg = (): JSX.Element => {
  return (
    <svg xmlns='http://www.w3.org/2000/svg' width={30} height={30} fill='none'>
      <rect width={29} height={29} x={0.5} y={0.5} stroke='#DBE3F5' rx={14.5} />
      <path
        stroke='#111'
        strokeLinecap='round'
        strokeLinejoin='round'
        strokeWidth={1.2}
        d='M11 15h8.114'
      />
    </svg>
  );
};

const renderPlusSvg = (): JSX.Element => {
  return (
    <svg xmlns='http://www.w3.org/2000/svg' width={30} height={30} fill='none'>
      <rect width={29} height={29} x={0.5} y={0.5} stroke='#DBE3F5' rx={14.5} />
      <path
        stroke='#111'
        strokeLinecap='round'
        strokeLinejoin='round'
        strokeWidth={1.2}
        d='M14.955 10.916v8.167M10.898 15h8.114'
      />
    </svg>
  );
};

export const ProductCounter: React.FC<Props> = ({item}): JSX.Element => {
  const dispatch = hooks.useAppDispatch();

  const addToCartButton = (): JSX.Element => {
    return (
      <div
        style={{borderRadius: '50%'}}
        onClick={() => {
          dispatch(actions.addToCart(item));
        }}
      >
        {renderPlusSvg()}
      </div>
    );
  };

  const removeFromCartButton = (): JSX.Element => {
    return (
      <div
        style={{borderRadius: '50%'}}
        onClick={() => {
          dispatch(actions.removeFromCart(item));
        }}
      >
        {renderMinusSvg()}
      </div>
    );
  };

  const renderQty = (): JSX.Element => {
    return (
      <span
        style={{
          // ...theme.fonts.Mulish_400Regular,
          fontSize: 12,
          color: theme.colors.textColor,
        }}
      >
        {item.quantity}
      </span>
    );
  };

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
        alignItems: 'center',
        height: '100%',
        padding: '0 20px',
      }}
    >
      {addToCartButton()}
      {renderQty()}
      {removeFromCartButton()}
    </div>
  );
};
