import axios from 'axios';
import {Swiper, SwiperSlide} from 'swiper/react';
import React, {useEffect, useState} from 'react';

import {text} from '../../text';
import {hooks} from '../../hooks';
import {URLS} from '../../config';
import {items} from '../../items';
import {custom} from '../../custom';
import {theme} from '../../constants';
import {components} from '../../components';
import {CategoryType, ProductType} from '../../types';

export const Categories: React.FC = () => {
  const dispatch = hooks.useAppDispatch();
  const navigate = hooks.useAppNavigate();

  const [tagsData, setTagsData] = useState<any>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [productsData, setProductsData] = useState<ProductType[]>([]);
  const [categoriesData, setCategoriesData] = useState<CategoryType[]>([]);

  const getData = async () => {
    setLoading(true);
    try {
      const products = await axios
        .get(URLS.GET_PRODUCTS)
        .then((res) => res.data.products);

      const categories = await axios
        .get(URLS.GET_CATEGORIES)
        .then((res) => res.data.categories);

      const tags = await axios.get(URLS.GET_TAGS).then((res) => res.data.tags);

      setTagsData(tags);
      setProductsData(products);
      setCategoriesData(categories);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getData();
    window.scrollTo(0, 0);
  }, []);

  const renderHeader = (): JSX.Element => {
    return <components.Header burger={true} basket={true} line={true} />;
  };

  const renderTags = (): JSX.Element => {
    return (
      <Swiper
        spaceBetween={14}
        slidesPerView={'auto'}
        pagination={{clickable: true}}
      >
        {tagsData.map((item: any, index: number) => {
          return (
            <SwiperSlide
              style={{
                height: 90,
                width: 90,
                borderRadius: 10,
                cursor: 'pointer',
                position: 'relative',
                display: 'flex',
                flexDirection: 'column',
                padding: '10px 10px 8px 10px',
                justifyContent: 'space-between',
                backgroundColor: theme.colors.imageBackground,
              }}
            >
              <custom.View
                key={index}
                style={{
                  backgroundColor: theme.colors.lavenderMist,
                  padding: 8,
                  borderRadius: 20,
                  marginBottom: 10,
                }}
              >
                <h3 style={{color: theme.colors.black}}>{item.name}</h3>
              </custom.View>
            </SwiperSlide>
          );
        })}
      </Swiper>
    );
  };

  const renderCategories = (): JSX.Element => {
    return (
      <div
        style={{
          gap: 1,
          display: 'flex',
          flexWrap: 'wrap',
          flexDirection: 'row',
          justifyContent: 'space-between',
        }}
      >
        {categoriesData.map((item, index) => {
          const divisibleByThree = (index - 2) % 3 === 0;
          return (
            <custom.View
              style={{
                position: 'relative',
                width: divisibleByThree ? '100%' : 'calc(50% - 0.5px)',
                height: divisibleByThree ? 170 : 187,
                backgroundColor: theme.colors.imageBackground,
                padding: 20,
                justifyContent: 'flex-end',
                cursor: 'pointer',
              }}
              onClick={() => {
                navigate('/Shop', {state: {products: productsData}});
              }}
            >
              <img
                style={{
                  width: '100%',
                  height: '100%',
                  objectFit: 'cover',
                  position: 'absolute',
                  inset: 0,
                  backgroundColor: theme.colors.imageBackground,
                }}
                src={item.image}
              />
              <div
                style={{
                  position: 'absolute',
                  inset: 0,
                  backgroundColor: 'rgba(23, 28, 49, 0.4)',
                }}
              />
              <text.H3
                style={{
                  color: theme.colors.white,
                  zIndex: 1,
                }}
                numberOfLines={1}
              >
                {item.name}
              </text.H3>
            </custom.View>
          );
        })}
      </div>
    );
  };

  const renderContent = (): JSX.Element => {
    return (
      <main style={{paddingTop: 40, paddingBottom: 64 + 30, marginTop: 52}}>
        {renderTags()}
        {renderCategories()}
      </main>
    );
  };

  const renderBottomTabBar = () => {
    return <components.BottomTabBar />;
  };

  return (
    <>
      {renderHeader()}
      {renderContent()}
      {renderBottomTabBar()}
    </>
  );
};
