import axios from 'axios';
import React, {useEffect, useState} from 'react';
import useEmblaCarousel from 'embla-carousel-react';

import {text} from '../text';
import {utils} from '../utils';
import {URLS} from '../config';
import {hooks} from '../hooks';
import {items} from '../items';
import {custom} from '../custom';
import {svg} from '../assets/svg';
import {components} from '../components';

const creditCards = [
  {
    id: 1,
    image: require('../assets/other/02.png'),
  },
  {
    id: 2,
    image: require('../assets/other/03.png'),
  },
];

export const PaymentMethod: React.FC = () => {
  const navigate = hooks.useAppNavigate();

  const [emblaRef, emblaApi] = useEmblaCarousel();
  const [activeIndex, setActiveIndex] = useState(0);

  const [productsData, setProductsData] = useState([]);

  const getData = async () => {
    // setLoading(true);

    try {
      const products = await axios
        .get(URLS.GET_PRODUCTS)
        .then((res) => res.data.products);

      setProductsData(products);
      // setCategoriesData(categories);
    } catch (error) {
      console.error(error);
    } finally {
      // setLoading(false);
    }
  };

  useEffect(() => {
    getData();
    window.scrollTo(0, 0);
  }, []);

  const renderHeader = (): JSX.Element => {
    return <components.Header title='Payment Method' goBack={true} />;
  };

  const renderContent = (): JSX.Element => {
    return (
      <main style={{padding: '78px 0 0 0'}}>
        <div style={{padding: '0 20px', ...utils.rowCenterSpcBtw()}}>
          <text.H5>Cards</text.H5>
          <div
            style={{...utils.rowCenter()}}
            onClick={() => {
              navigate('/AddANewCard');
            }}
          >
            <text.T14 style={{marginRight: 14}}>Add a new card</text.T14>
            <svg.PlusSvg />
          </div>
        </div>
        <div style={{display: 'block', flexDirection: 'initial'}}>
          <custom.ScrollView style={{paddingLeft: 20, paddingRight: 20}}>
            {creditCards.map((card) => {
              return (
                <img
                  key={card.id}
                  src={card.image}
                  alt='Card'
                  style={{width: 200}}
                />
              );
            })}
          </custom.ScrollView>
        </div>
        <custom.ScrollView style={{paddingLeft: 20, paddingRight: 20}}>
          {productsData?.map((item: any, index: number, arra: any) => {
            const isLast = index === arra.length - 1;
            return (
              <items.ProductCard
                key={item.id}
                isLast={isLast}
                item={item}
                version={1}
              />
            );
          })}
        </custom.ScrollView>
      </main>
    );
  };

  return (
    <div id='screen'>
      {renderHeader()}
      {renderContent()}
    </div>
  );
};
