import axios from 'axios';
import React, {useEffect, useState} from 'react';

import {text} from '../../text';
import {hooks} from '../../hooks';
import {URLS} from '../../config';
import {svg} from '../../assets/svg';
import {actions} from '../../store/actions';
import {components} from '../../components';

export const CartEmpty: React.FC = () => {
  const navigate = hooks.useAppNavigate();
  const dispatch = hooks.useAppDispatch();

  const [loading, setLoading] = React.useState(false);
  const [productsData, setProductsData] = useState<any>([]);

  const getData = async () => {
    setLoading(true);

    try {
      const products = await axios
        .get(URLS.GET_PRODUCTS)
        .then((res) => res.data.products);

      setProductsData(products);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    getData();
    window.scrollTo(0, 0);
  }, []);

  const renderHeader = (): JSX.Element => {
    return (
      <components.Header burger={true} basket={true} line={true} title='Cart' />
    );
  };

  const renderContent = (): JSX.Element => {
    return (
      <main
        style={{
          paddingTop: 40,
          paddingBottom: 64 + 30,
          marginTop: 52,
          paddingLeft: 20,
          paddingRight: 20,
        }}
      >
        <svg.ShoppingBagSvg style={{alignSelf: 'center', marginBottom: 20}} />
        <components.Line style={{marginBottom: 14}} />
        <text.H2 style={{textAlign: 'center', marginBottom: 14}}>
          Your cart is empty!
        </text.H2>
        <text.T16 style={{textAlign: 'center', whiteSpace: 'pre-line'}}>
          Looks like you haven't made{'\n'}your order yet.
        </text.T16>
        <components.Button
          title='shop now'
          onClick={() => {
            dispatch(actions.resetFilters());
            navigate('/Shop', {state: {products: productsData}});
          }}
          style={{marginTop: 20}}
        />
      </main>
    );
  };

  const renderBottomTabBar = () => {
    return <components.BottomTabBar />;
  };

  return (
    <>
      {renderHeader()}
      {renderContent()}
      {renderBottomTabBar()}
    </>
  );
};
