import React from 'react';

type Props = {
  className?: string;
  onClick?: () => void;
  children: React.ReactNode;
  style?: React.CSSProperties;
};

export const View: React.FC<Props> = ({
  children,
  className,
  style,
  onClick,
}) => {
  return (
    <div
      className={className}
      style={{
        display: 'flex',
        flexDirection: 'column',
        position: 'relative',
        ...style,
      }}
      onClick={onClick}
    >
      {children}
    </div>
  );
};
