import React, {useState} from 'react';

import {text} from '../text';
import {utils} from '../utils';
import {hooks} from '../hooks';
import {custom} from '../custom';
import {svg} from '../assets/svg';
import {components} from '../components';

export const AddANewAddress: React.FC = () => {
  const navigate = hooks.useAppNavigate();

  const [selectedLocation, setSelectedLocation] = useState(false);

  const renderHeader = (): JSX.Element => {
    return <components.Header title='Add a new address' goBack={true} />;
  };

  const renderContent = (): JSX.Element => {
    return (
      <main style={{padding: '52px 0 20px 0'}}>
        <img
          alt='map'
          src={require('../assets/other/01.jpg')}
          style={{width: '100%', marginBottom: 40}}
        />
        <div style={{padding: '0 20px'}}>
          <custom.InputField
            label='title'
            placeholder='Enter your address'
            containerStyle={{marginBottom: 20}}
          />
          <custom.InputField
            label='enter a new address'
            placeholder='3646 S 58th Ave, Cicero, IL 60804, U...'
            containerStyle={{marginBottom: 20}}
          />
          <div
            style={{...utils.rowCenter(), cursor: 'pointer', marginBottom: 20}}
            onClick={() => {
              setSelectedLocation(!selectedLocation);
            }}
          >
            <div
              style={{
                width: 18,
                height: 18,
                borderRadius: 5,
                border: '2px solid #E8EFF4',
                ...utils.flexCenter(),
              }}
            >
              {selectedLocation && <svg.InputCheckSvg />}
            </div>
            <text.T14 style={{marginLeft: 10}}>Use current location</text.T14>
          </div>
          <components.Button
            title='save address'
            onClick={() => {
              navigate(-1);
            }}
          />
        </div>
      </main>
    );
  };

  return (
    <div id='screen'>
      {renderHeader()}
      {renderContent()}
    </div>
  );
};
