import axios from 'axios';
import {useLocation} from 'react-router-dom';
import {useNavigate} from 'react-router-dom';
import React, {useEffect, useState} from 'react';
import useEmblaCarousel from 'embla-carousel-react';

import {text} from '../text';
import {utils} from '../utils';
import {items} from '../items';
import {URLS} from '../config';
import {hooks} from '../hooks';
import {svg} from '../assets/svg';
import {theme} from '../constants';
import {product} from '../product';
import {CarouselType} from '../types';
import {actions} from '../store/actions';
import {components} from '../components';

export const Product: React.FC = () => {
  const location = useLocation();
  const item = location.state.item;

  const navigate = useNavigate();
  const dispatch = hooks.useAppDispatch();

  const [emblaRef, emblaApi] = useEmblaCarousel();

  const handleSlideChange = () => {
    if (emblaApi) {
      setActiveIndex(emblaApi.selectedScrollSnap());
    }
  };

  useEffect(() => {
    if (emblaApi) {
      emblaApi.on('select', handleSlideChange);
    }
  }, [emblaApi]);

  const [reviewsData, setReviewsData] = useState<any>([]);
  const [isLoading, setIsLoading] = useState<boolean>(false);

  const [selectedColor, setSelectedColor] = useState<string>(
    item.colors?.[1].name
  );

  const modifiedItem = {
    ...item,
    color: selectedColor,
  };

  const [activeIndex, setActiveIndex] = useState(0);

  const getReviews = async () => {
    setIsLoading(true);
    try {
      const reviews = await axios
        .get(URLS.GET_REVIEWS)
        .then((res) => res.data.reviews);

      setReviewsData(reviews);
    } catch (error) {
      console.error(error);
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    window.scrollTo(0, 0);
    getReviews();
  }, []);

  const renderHeader = (): JSX.Element => {
    return <components.Header goBack={true} basket={true} />;
  };

  const renderCarousel = (): JSX.Element => {
    return (
      <section className='embla' ref={emblaRef} style={{marginBottom: 20}}>
        <div className='embla__container'>
          {item.images?.map((item: any, index: any, array: any) => {
            return (
              <div
                style={{
                  height: 350,
                  width: '100%',
                  cursor: 'pointer',
                  backgroundColor: theme.colors.imageBackground,
                }}
                key={index}
                className='embla__slide'
              >
                <img
                  src={item}
                  alt='Carousel'
                  style={{
                    width: '100%',
                    height: '100%',
                    objectFit: 'contain',
                  }}
                />
              </div>
            );
          })}
        </div>
      </section>
    );
  };

  const renderIndicator = (): JSX.Element => {
    return (
      <section
        style={{
          gap: 5,
          ...utils.rowCenter(),
          justifyContent: 'center',
          marginBottom: 20,
        }}
      >
        {item.images?.map((item: CarouselType, index: number) => {
          const isSelected = index === activeIndex;
          return <items.DotItem key={index} isSelected={isSelected} />;
        })}
      </section>
    );
  };

  const renderNameWithButton = (): JSX.Element => {
    return (
      <div style={{padding: '0 20px', ...utils.rowCenterSpcBtw()}}>
        <text.H3 numberOfLines={1}>{item.name}</text.H3>
        <product.ProductInWishlist item={modifiedItem} version={2} />
      </div>
    );
  };

  const renderRating = (): JSX.Element => {
    return (
      <div style={{padding: '0 20px 5px 20px'}}>
        <product.ProductRating rating={4} ratingCount={23} />
      </div>
    );
  };

  const renderPriceWithQuantity = (): JSX.Element => {
    return (
      <div style={{padding: '0 20px 15px 20px', ...utils.rowCenterSpcBtw()}}>
        <div
          style={{
            fontSize: 20,
            ...theme.fonts.Mulish_600SemiBold,
            color: theme.colors.mainColor,
          }}
        >
          ${item.price.toFixed(2)}
        </div>
        <product.ProductCounterInner item={modifiedItem} />
      </div>
    );
  };

  const renderSizes = (): JSX.Element => {
    return (
      <div style={{padding: '0 20px 25px 20px'}}>
        <text.H5 style={{marginBottom: 14}} numberOfLines={1}>
          Size
        </text.H5>
        <div style={{gap: 7, flexWrap: 'wrap', ...utils.rowCenter()}}>
          {item.sizes?.map((item: any, index: number) => {
            return (
              <button
                key={index}
                style={{
                  width: 50,
                  height: 50,
                  fontSize: 12,
                  borderRadius: 25,
                  cursor: 'pointer',
                  ...utils.flexCenter(),
                  color: theme.colors.mainColor,
                  ...theme.fonts.Mulish_600SemiBold,
                  border: `1px solid ${theme.colors.lavenderMist}`,
                }}
              >
                {item}
              </button>
            );
          })}
        </div>
      </div>
    );
  };

  const renderColors = (): JSX.Element => {
    return (
      <div style={{marginBottom: 40, padding: '0 20px 0 20px'}}>
        <text.H5 style={{marginBottom: 20, color: theme.colors.mainColor}}>
          Color
        </text.H5>
        <div
          style={{
            gap: 10,
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
            flexWrap: 'wrap',
          }}
        >
          {item.colors?.map((item: any, index: number) => {
            return (
              <button
                key={index}
                style={{
                  width: 30,
                  height: 30,
                  borderRadius: 15,
                  flexDirection: 'row',
                  alignItems: 'center',
                  backgroundColor:
                    item === 'carrot'
                      ? '#FF6262'
                      : item === 'blue'
                      ? '#63C7FF'
                      : item === 'beige'
                      ? '#F8E7CD'
                      : item === 'purple'
                      ? '#323858'
                      : item === 'black'
                      ? '#111111'
                      : '',
                  justifyContent: 'center',
                  flexWrap: 'wrap',
                  border:
                    item === 'White' ? '#E7EBEB' : theme.colors.transparent,
                  borderStyle: 'solid',
                  borderWidth: 1,
                }}
                onClick={() => {
                  setSelectedColor(item.name);
                }}
              >
                {/* {selectedColor === item.name && (
                  <svg.CheckSvg
                    color={
                      item.name === 'White'
                        ? theme.colors.mainColor
                        : theme.colors.white
                    }
                  />
                )} */}
              </button>
            );
          })}
        </div>
      </div>
    );
  };

  const renderDescription = (): JSX.Element => {
    return (
      <div style={{marginBottom: 0, padding: '0 20px 20px 20px'}}>
        <text.H5 style={{marginBottom: 14, color: theme.colors.mainColor}}>
          Description
        </text.H5>
        <text.T16 style={{marginBottom: 0, color: theme.colors.textColor}}>
          {item.description}
        </text.T16>
      </div>
    );
  };

  const renderButton = (): JSX.Element => {
    return (
      <components.Button
        title='Add to cart'
        onClick={() => {
          dispatch(actions.addToCart(modifiedItem));
        }}
        containerStyle={{marginBottom: 40, padding: '0 20px 0 20px'}}
      />
    );
  };

  const renderReviews = (): JSX.Element => {
    const reviewQty = 2;
    return (
      <div style={{marginBottom: 20}}>
        <components.BlockHeading
          title={`Reviews (${reviewsData.slice(0, reviewQty).length})`}
          containerStyle={{padding: '0 20px 0 20px', marginBottom: 20}}
          viewAllOnClick={() =>
            navigate('/reviews', {state: {reviews: reviewsData}})
          }
        />
        {reviewsData
          .slice(0, reviewQty)
          .map((item: any, index: number, array: any) => {
            const isLast = index === array.length - 1;
            return (
              <items.ReviewItem
                item={item}
                isLast={isLast}
                key={item.id.toString()}
              />
            );
          })}
      </div>
    );
  };

  const renderContent = (): JSX.Element => {
    if (isLoading) return <components.Loader />;

    return (
      <main style={{padding: '0 0 20px 0', marginTop: 52}}>
        {renderCarousel()}
        {renderIndicator()}
        {renderNameWithButton()}
        {renderRating()}
        {renderPriceWithQuantity()}
        {renderSizes()}
        {renderColors()}
        {renderDescription()}
        {renderButton()}
        {renderReviews()}
      </main>
    );
  };

  return (
    <>
      {renderHeader()}
      {renderContent()}
    </>
  );
};
