import axios from 'axios';
import React, {useEffect, useState} from 'react';

import {URLS} from '../config';
import {hooks} from '../hooks';
import {custom} from '../custom';
import {svg} from '../assets/svg';
import {theme} from '../constants';
import {components} from '../components';

export const EditProfile: React.FC = () => {
  const navigate = hooks.useAppNavigate();

  const [user, setUser] = useState<any>(null);
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    getData();
    window.scrollTo(0, 0);
  }, []);

  const getData = async () => {
    setLoading(true);

    try {
      const user = await axios.get(URLS.GET_USER).then((res) => res.data.user);

      setUser(user);
    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const renderHeader = (): JSX.Element => {
    return <components.Header title='Edit Profile' goBack={true} />;
  };

  const renderContent = (): JSX.Element => {
    if (loading) return <components.Loader />;

    return (
      <main style={{padding: '75px 20px 20px 20px'}}>
        <components.Line style={{marginBottom: 20}} />
        <div
          style={{
            height: 120,
            width: 120,
            borderRadius: '50%',
            alignSelf: 'center',
            marginBottom: 46,
            position: 'relative',
            cursor: 'pointer',
          }}
          onClick={() => {}}
        >
          <img
            src={user?.photo}
            style={{
              height: '100%',
              width: '100%',
              borderRadius: '50%',
              border: `6px solid ${theme.colors.lavenderMist}`,
            }}
          />
          <svg.CameraSvg style={{position: 'absolute', bottom: 0, right: 0}} />
        </div>
        <custom.InputField
          label='Name'
          icon={<svg.InputCheckSvg />}
          placeholder='Kristin Watson'
          containerStyle={{marginBottom: 20}}
        />
        <custom.InputField
          label='Email'
          icon={<svg.InputCheckSvg />}
          placeholder='kristinwatson@mail.com'
          containerStyle={{marginBottom: 20}}
        />
        <custom.InputField
          label='Phone number'
          icon={<svg.InputCheckSvg />}
          placeholder='+17 123456789'
          containerStyle={{marginBottom: 20}}
        />
        <custom.InputField
          label='location'
          icon={<svg.InputCheckSvg />}
          placeholder='Chicago, USA'
          containerStyle={{marginBottom: 20}}
        />
        <components.Button
          title='save changes'
          onClick={() => {
            navigate(-1);
          }}
        />
      </main>
    );
  };

  return (
    <div id='screen'>
      {renderHeader()}
      {renderContent()}
    </div>
  );
};
