import React, {useState} from 'react';

import {text} from '../text';
import {hooks} from '../hooks';
import {utils} from '../utils';
import {custom} from '../custom';
import {svg} from '../assets/svg';
import {theme} from '../constants';
import {components} from '../components';

export const SignIn: React.FC = () => {
  const navigate = hooks.useAppNavigate();

  const [rememberMe, setRememberMe] = useState<boolean>(false);

  const renderHeader = (): JSX.Element => {
    return <components.Header title='Sign In' />;
  };

  const renderContent = (): JSX.Element => {
    return (
      <main
        style={{
          flex: 1,
          display: 'flex',
          flexDirection: 'column',
          padding: '0 20px 0 20px',
          paddingTop: '18%',
          marginTop: 52,
        }}
      >
        <components.Line style={{marginBottom: 14}} />
        <text.H1
          numberOfLines={1}
          style={{textAlign: 'center', marginBottom: 14}}
        >
          Welcome Back!
        </text.H1>
        <text.T16 style={{textAlign: 'center', marginBottom: 40}}>
          Sign in to continue
        </text.T16>
        <custom.InputField
          label='Email'
          icon={<svg.CheckSvg />}
          containerStyle={{marginBottom: 20}}
          placeholder='kristinwatson@mail.com'
        />
        <custom.InputField
          label='Password'
          icon={<svg.EyeOffSvg />}
          containerStyle={{marginBottom: 20}}
          placeholder='kristinwatson@mail.com'
        />
        <div style={{...utils.rowCenterSpcBtw(), marginBottom: 30}}>
          {/* Remember me */}
          <div
            style={{cursor: 'pointer', ...utils.rowCenterSpcBtw()}}
            onClick={() => setRememberMe(!rememberMe)}
          >
            <div
              style={{
                width: 18,
                height: 18,
                borderRadius: 5,
                border: '2px solid #E8EFF4',
                ...utils.flexCenter(),
              }}
            >
              {rememberMe && <svg.InputCheckSvg />}
            </div>
            <text.T16 style={{marginLeft: 12}}>Remember me</text.T16>
          </div>
          {/* Forgot password */}
          <text.T16
            style={{cursor: 'pointer', color: theme.colors.mainColor}}
            onClick={() => navigate('/ForgotPassword')}
          >
            Forgot password?
          </text.T16>
        </div>
        <components.Button
          title='Sign in'
          containerStyle={{marginBottom: 20}}
          onClick={() => {
            navigate('/TabNavigator');
          }}
        />
        <div style={{...utils.rowCenter(), marginBottom: 40}}>
          <text.T16 style={{marginRight: 4}}>Dont have an account?</text.T16>
          <text.T16
            onClick={() => navigate('/SignUp')}
            style={{cursor: 'pointer', color: theme.colors.mainColor}}
          >
            Sign up.
          </text.T16>
        </div>
        <div
          style={{...utils.rowCenter(), alignSelf: 'center', marginBottom: 20}}
        >
          <svg.FaceBookSvg
            style={{margin: '0 5px', cursor: 'pointer', borderRadius: '50%'}}
            onClick={() => {
              console.log('FaceBookSvg');
            }}
          />
          <svg.TwitterSvg
            style={{margin: '0 5px', cursor: 'pointer', borderRadius: '50%'}}
            onClick={() => {
              console.log('FaceBookSvg');
            }}
          />
          <svg.GoogleSvg
            style={{margin: '0 5px', cursor: 'pointer', borderRadius: '50%'}}
            onClick={() => {
              console.log('FaceBookSvg');
            }}
          />
        </div>
      </main>
    );
  };

  return (
    <custom.View style={{height: '100%'}}>
      {renderHeader()}
      {renderContent()}
    </custom.View>
  );
};
