import React from 'react';

import {text} from '../../text';
import {items} from '../../items';
import {utils} from '../../utils';
import {svg} from '../../assets/svg';
import {theme} from '../../constants';
import {components} from '../../components';
import {hooks, RootState} from '../../hooks';

export const Order: React.FC = () => {
  const navigate = hooks.useAppNavigate();

  const {subtotal, delivery, discount, list, total, discountAmount} =
    hooks.useAppSelector((state: RootState) => state.cartSlice);

  const renderHeader = (): JSX.Element => {
    return (
      <components.Header
        burger={true}
        basket={true}
        line={true}
        title='Order'
      />
    );
  };

  const renderProducts = (): JSX.Element | null => {
    if (list.length > 0) {
      return (
        <div
          style={{
            marginBottom: 30,
            borderBottom: `1px solid ${theme.colors.lavenderMist}`,
          }}
        >
          {list?.map((item, index, array) => {
            const isLast = index === array.length - 1;
            return <items.OrderItem key={index} item={item} isLast={isLast} />;
          })}
        </div>
      );
    }

    return null;
  };

  const renderPromocodeApplied = (): JSX.Element | null => {
    return (
      <div style={{...utils.rowCenter(), padding: '0 20px 30px 20px'}}>
        <text.T16 style={{marginRight: 8}}>Promocode applied</text.T16>
        <svg.OrderCheckSvg />
      </div>
    );
  };

  const renderTotal = (): JSX.Element => {
    return (
      <div
        style={{
          marginBottom: '10%',
        }}
      >
        <div
          style={{
            ...utils.rowCenterSpcBtw(),
            padding: '0 20px',
            marginBottom: 10,
          }}
        >
          <text.H5>Subtotal</text.H5>
          <text.H5>${subtotal.toFixed(2)}</text.H5>
        </div>
        <div
          style={{
            ...utils.rowCenterSpcBtw(),
            padding: '0 20px',
            marginBottom: 10,
          }}
        >
          <text.T16>Discount</text.T16>
          <text.T16>{discountAmount}</text.T16>
        </div>
        <div
          style={{
            ...utils.rowCenterSpcBtw(),
            padding: '0 20px',
            paddingBottom: 10,
            marginBottom: 10,
            borderBottom: `1px solid ${theme.colors.lavenderMist}`,
          }}
        >
          <text.T16>Delivery</text.T16>
          <text.T16
            style={{
              color: '#00824B',
            }}
          >{`${delivery === 0 ? 'Free' : `$${delivery.toFixed(2)}`}`}</text.T16>
        </div>
        <div
          style={{
            ...utils.rowCenterSpcBtw(),
            padding: '0 20px',
            marginBottom: 10,
          }}
        >
          <text.H4>Total</text.H4>
          <text.H4>${total.toFixed(2)}</text.H4>
        </div>
      </div>
    );
  };

  const renderButton = (): JSX.Element => {
    return (
      <components.Button
        title='proceed to checkout'
        containerStyle={{
          padding: '0 20px',
        }}
        onClick={() => navigate('/Checkout')}
      />
    );
  };

  const renderContent = (): JSX.Element => {
    return (
      <main style={{padding: '72px 0 100px 0'}}>
        {renderProducts()}
        {renderPromocodeApplied()}
        {renderTotal()}
        {renderButton()}
      </main>
    );
  };

  const renderBottomTabBar = () => {
    return <components.BottomTabBar />;
  };

  return (
    <div id='screen'>
      {renderHeader()}
      {renderContent()}
      {renderBottomTabBar()}
    </div>
  );
};
