import axios from 'axios';
import React, {useState, useEffect} from 'react';

import {text} from '../text';
import {utils} from '../utils';
import {hooks} from '../hooks';
import {URLS} from '../config';
import {custom} from '../custom';
import {svg} from '../assets/svg';
import {theme} from '../constants';
import {RootState} from '../store';
import {actions} from '../store/actions';
import {components} from '../components';

const labels = ['sale', 'new', 'top'];
const rating = [
  {
    id: 1,
    rating: 1,
    title: '1,0',
  },
  {
    id: 2,
    rating: 2,
    title: '2,0',
  },
  {
    id: 3,
    rating: 3,
    title: '3,0',
  },
  {
    id: 4,
    rating: 4,
    title: '4,0',
  },
  {
    id: 5,
    rating: 5,
    title: '5,0',
  },
];

export const Filter: React.FC = () => {
  const navigate = hooks.useAppNavigate();
  const dispatch = hooks.useAppDispatch();

  const [sizes, setSizes] = useState<string[]>([]);
  const [tagsData, setTagsData] = useState<any>([]);
  const [colors, setColors] = useState<string[]>([]);

  const [isLoading, setIsLoading] = useState<boolean>(true);

  const {selectedTags, selectedSizes, selectedColors, selectedCategories} =
    hooks.useAppSelector((state: RootState) => state.filterSlice);

  const getData = async () => {
    setIsLoading(true);

    try {
      const sizes = await axios
        .get(URLS.GET_SIZES)
        .then((res) => res.data.sizes);

      const colors = await axios
        .get(URLS.GET_COLORS)
        .then((res) => res.data.colors);

      const tags = await axios.get(URLS.GET_TAGS).then((res) => res.data.tags);

      setSizes(sizes);
      setColors(colors);
      setTagsData(tags);
    } catch (error) {
      console.log(error);
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    window.scrollTo(0, 0);
    getData();
  }, []);

  const renderHeader = (): JSX.Element => {
    return <components.Header goBack={true} title='Filter' />;
  };

  const renderSizes = (): JSX.Element => {
    return (
      <div style={{marginBottom: 25}}>
        <text.H5 style={{marginBottom: 14, color: theme.colors.mainColor}}>
          Size
        </text.H5>
        <div style={{...utils.rowCenterWrap(), gap: 7}}>
          {sizes?.map((item: any, index: number) => {
            return (
              <button
                key={index}
                style={{
                  width: '14%',
                  aspectRatio: 1,
                  borderRadius: '50%',
                  cursor: 'pointer',
                  borderStyle: 'solid',
                  ...utils.flexCenter(),
                  border: `1px solid ${theme.colors.lavenderMist}`,
                  backgroundColor: selectedSizes.includes(item)
                    ? theme.colors.lavenderMist
                    : theme.colors.white,
                }}
                onClick={() => {
                  dispatch((dispatch, getState) => {
                    const selectedSizes = getState().filterSlice.selectedSizes;
                    if (selectedSizes.includes(item)) {
                      dispatch(
                        actions.setSelectedSizes(
                          selectedSizes.filter((size) => size !== item)
                        )
                      );
                    } else {
                      dispatch(
                        actions.setSelectedSizes([...selectedSizes, item])
                      );
                    }
                  });
                }}
              >
                <text.T12
                  style={{
                    color: theme.colors.mainColor,
                    ...theme.fonts.Mulish_600SemiBold,
                  }}
                >
                  {item}
                </text.T12>
              </button>
            );
          })}
        </div>
      </div>
    );
  };

  const renderColors = (): JSX.Element => {
    return (
      <div style={{marginBottom: 30}}>
        <text.H5 style={{marginBottom: 14, color: theme.colors.mainColor}}>
          Color
        </text.H5>
        <div style={{...utils.rowCenterWrap(), gap: 10}}>
          {colors?.map((item: any, index: number) => {
            return (
              <button
                key={index}
                style={{
                  width: 34,
                  height: 34,
                  borderRadius: 17,
                  cursor: 'pointer',
                  borderStyle: 'solid',
                  ...utils.flexCenter(),
                  backgroundColor: item.code,
                  border: `4px solid ${
                    selectedColors.includes(item.name)
                      ? theme.colors.lavenderMist
                      : theme.colors.white
                  }`,
                }}
                onClick={() => {
                  dispatch((dispatch, getState) => {
                    const selectedColors =
                      getState().filterSlice.selectedColors;
                    if (selectedColors.includes(item.name)) {
                      dispatch(
                        actions.setSelectedColors(
                          selectedColors.filter((color) => color !== item.name)
                        )
                      );
                    } else {
                      dispatch(
                        actions.setSelectedColors([
                          ...selectedColors,
                          item.name,
                        ])
                      );
                    }
                  });
                }}
              ></button>
            );
          })}
        </div>
      </div>
    );
  };

  const renderLabels = (): JSX.Element => {
    return (
      <div style={{marginBottom: 40, gap: '14%', ...utils.rowCenterWrap()}}>
        {labels.map((item, index) => {
          return (
            <div
              key={index}
              style={{
                display: 'flex',
                alignItems: 'center',
                cursor: 'pointer',
                flexDirection: 'row',
              }}
              onClick={() => {
                dispatch((dispatch, getState) => {
                  const selectedCategories =
                    getState().filterSlice.selectedCategories;
                  if (selectedCategories.includes(item)) {
                    dispatch(
                      actions.setSelectedCategories(
                        selectedCategories.filter(
                          (category) => category !== item
                        )
                      )
                    );
                  } else {
                    dispatch(
                      actions.setSelectedCategories([
                        ...selectedCategories,
                        item,
                      ])
                    );
                  }
                });
              }}
            >
              <div
                style={{
                  width: 18,
                  height: 18,
                  marginRight: 8,
                  borderRadius: 5,
                  border: '2px solid #E8EFF4',
                  ...utils.flexCenter(),
                }}
              >
                {selectedCategories.includes(item) && <svg.InputCheckSvg />}
              </div>
              <div
                style={{
                  margin: 0,
                  padding: '0px 6px',
                  display: 'flex',
                  // backgroundColor: selectedCategories.includes(item)
                  //   ? theme.colors.pastelMint
                  //   : '#FAFAFA',
                  backgroundColor:
                    item === 'sale'
                      ? '#69864D'
                      : item === 'top'
                      ? '#864D7D'
                      : item === 'new'
                      ? '#CFA93F'
                      : '',
                }}
              >
                <span
                  style={{
                    textTransform: 'uppercase',
                    color: theme.colors.white,
                    fontSize: 10,
                    lineHeight: 1.7,
                    ...theme.fonts.Mulish_700Bold,
                  }}
                >
                  {item}
                </span>
              </div>
            </div>
          );
        })}
      </div>
    );
  };

  const renderTags = (): JSX.Element => {
    return (
      <custom.View style={{marginBottom: 20}}>
        <text.H5 style={{marginBottom: 14, color: theme.colors.mainColor}}>
          Tags
        </text.H5>
        <div style={{display: 'flex', flexWrap: 'wrap', gap: 8}}>
          {tagsData.map((item: any) => {
            return (
              <div
                key={item.id}
                style={{
                  fontSize: 12,
                  lineHeight: 1.7,
                  borderRadius: 50,
                  padding: '5px 20px',
                  textTransform: 'uppercase',
                  ...theme.fonts.Mulish_600SemiBold,
                  border: `1px solid ${theme.colors.lavenderMist}`,
                  backgroundColor: selectedTags.includes(item.name)
                    ? theme.colors.lavenderMist
                    : theme.colors.white,
                }}
                onClick={() => {
                  dispatch((dispatch, getState) => {
                    const selectedTags = getState().filterSlice.selectedTags;
                    if (selectedTags.includes(item.name)) {
                      dispatch(
                        actions.setSelectedTags(
                          selectedTags.filter((tag) => tag !== item.name)
                        )
                      );
                    } else {
                      dispatch(
                        actions.setSelectedTags([...selectedTags, item.name])
                      );
                    }
                  });
                }}
              >
                {item.name}
              </div>
            );
          })}
        </div>
      </custom.View>
    );
  };

  const renderButton = (): JSX.Element => {
    return (
      <components.Button
        title='apply filters'
        onClick={() => {
          navigate(-1);
        }}
      />
    );
  };

  const renderContent = (): JSX.Element => {
    if (isLoading) return <components.Loader />;

    return (
      <main style={{padding: '30px 20px 20px 20px', marginTop: 52}}>
        {renderSizes()}
        {renderColors()}
        {renderLabels()}
        {renderTags()}
        {renderButton()}
      </main>
    );
  };

  return (
    <>
      {renderHeader()}
      {renderContent()}
    </>
  );
};
