import React, {useState} from 'react';

import {text} from '../text';
import {hooks} from '../hooks';
import {svg} from '../assets/svg';
import {theme} from '../constants';
import {components} from '../components';

export const LeaveAReviews: React.FC = () => {
  const navigate = hooks.useAppNavigate();

  const [rating, setRating] = useState<number>(0);

  const renderHeader = () => {
    return <components.Header title='Leave a review' goBack={true} />;
  };

  const renderContent = () => {
    return (
      <main style={{padding: '78px 0px 20px 0px'}}>
        <svg.CommentSvg style={{alignSelf: 'center', marginBottom: 20}} />
        <components.Line style={{marginBottom: 14}} />
        <text.H2
          style={{
            textAlign: 'center',
            marginBottom: 20,
            whiteSpace: 'pre-line',
          }}
        >
          Please rate the quality of{'\n'}service for the order!
        </text.H2>
        <components.RatingStars
          rating={rating}
          setRating={setRating}
          containerStyle={{marginBottom: 20, alignSelf: 'center'}}
        />
        <text.T14
          style={{
            margin: '0 20px',
            textAlign: 'center',
            marginBottom: 38,
            whiteSpace: 'pre-line',
          }}
        >
          Your comments and suggestions help us improve{'\n'}the service quality
          better!
        </text.T14>
        <div style={{position: 'relative'}}>
          <span
            style={{
              position: 'absolute',
              top: -8,
              left: 20,
              padding: '0 10px',
              borderRadius: 12,
              backgroundColor: '#fff',
              ...theme.fonts.Mulish_600SemiBold,
              fontSize: 12,
              textTransform: 'uppercase',
              color: theme.colors.textColor,
            }}
          >
            comment
          </span>
          <textarea
            className='input-field'
            style={{
              width: '100%',
              height: 130,
              borderColor: '#E8EFF4',
              marginBottom: 40,
              borderRadius: 12,
              display: 'block',
              boxSizing: 'border-box',
              verticalAlign: 'top',
              overflow: 'auto',
              textAlign: 'left',
              padding: '10px 30px',
              resize: 'none',
              color: theme.colors.mainColor,
            }}
            placeholder='Enter your comment'
          />
        </div>

        <components.Button
          title='submit'
          onClick={() => {
            navigate(-1);
          }}
        />
      </main>
    );
  };

  return (
    <div className='flex'>
      {renderHeader()}
      {renderContent()}
    </div>
  );
};
