import React from 'react';

import {hooks} from '../hooks';
import {utils} from '../utils';
import {svg} from '../assets/svg';
import {theme} from '../constants';

import {actions} from '../store/actions';

const tabs = [
  {
    id: 1,
    name: 'Home',
    icon: svg.HomeTabSvg,
  },
  {
    id: 2,
    name: 'Search',
    icon: svg.CategoryTabSvg,
  },
  {
    id: 3,
    name: 'Order',
    icon: svg.OrderTabSvg,
  },
  {
    id: 4,
    name: 'Wishlist',
    icon: svg.WishlistTabSvg,
  },
  {
    id: 5,
    name: 'Profile',
    icon: svg.ProfileTabSvg,
  },
];

export const BottomTabBar: React.FC = () => {
  const dispatch = hooks.useAppDispatch();

  const currentTabScreen = hooks.useAppSelector(
    (state) => state.tabSlice.screen
  );

  return (
    <nav
      id='app'
      className='container'
      style={{
        bottom: 0,
        zIndex: 4,
        width: '100%',
        position: 'fixed',
        borderRadius: '20px 20px 0 0',
      }}
    >
      <div
        style={{
          cursor: 'pointer',
          padding: '13px 20px',
          position: 'relative',
          ...utils.rowCenterSpcAround(),
          backgroundColor: theme.colors.white,
          borderTop: `1px solid ${theme.colors.lavenderMist}`,
        }}
      >
        {tabs.map((tab) => {
          return (
            <tab.icon
              key={tab.id}
              bgColor={
                currentTabScreen === tab.name
                  ? theme.colors.lavenderMist
                  : theme.colors.transparent
              }
              iconColor={
                currentTabScreen === tab.name
                  ? theme.colors.mainColor
                  : theme.colors.textColor
              }
              onClick={() => dispatch(actions.setScreen(tab.name))}
            />
          );
        })}
      </div>
    </nav>
  );
};
