import React, {useState} from 'react';

import {text} from '../text';
import {utils} from '../utils';
import {theme} from '../constants';
import {components} from '../components';

const creditCards = [
  {
    id: 1,
    name: 'Visa',
    number: '7741 ******** 6644',
  },
  {
    id: 2,
    name: 'Mastercard',
    number: '7674 ******** 1884',
  },
];

const paymentMethods = [
  {
    id: 1,
    method: 'Apple Pay',
  },
  {
    id: 2,
    method: 'Pay Pal',
  },
  {
    id: 3,
    method: 'Cash',
  },
];

export const CheckoutPaymentMethod: React.FC = () => {
  const [selectedCard, setSelectedCard] = useState(creditCards[0]);
  const [selectedMethod, setSelectedMethod] = useState(paymentMethods[0]);

  const renderHeader = (): JSX.Element => {
    return (
      <components.Header goBack={true} title='Payment method' line={true} />
    );
  };

  const renderContent = (): JSX.Element => {
    return (
      <main style={{padding: '72px 0 0 0'}}>
        <text.H4 style={{margin: '0 20px 14px 20px'}}>Credit cards</text.H4>
        <div
          style={{
            borderTop: `4px solid '#EAEFF9'`,
            borderBottom: `4px solid '#EAEFF9'`,
          }}
        >
          {creditCards.map((item, index, array) => {
            const isLast = index === array.length - 1;
            return (
              <div
                style={{
                  ...utils.rowCenterSpcBtw(),
                  padding: 20,
                  cursor: 'pointer',
                  backgroundColor: `${theme.colors.lavenderMist}30`,
                  borderBottom: isLast
                    ? 'none'
                    : `1px solid ${theme.colors.lavenderMist}`,
                }}
                key={item.id}
                onClick={() => setSelectedCard(item)}
              >
                <text.T16>{item.number}</text.T16>
                <div
                  style={{
                    width: 20,
                    height: 20,
                    borderRadius: 10,
                    border: '2px solid #DBE3F5',
                    ...utils.flexCenter(),
                  }}
                >
                  <div
                    style={{
                      width: 10,
                      height: 10,
                      borderRadius: 5,
                      backgroundColor:
                        selectedCard.id === item.id
                          ? theme.colors.accentColor
                          : theme.colors.transparent,
                    }}
                  />
                </div>
              </div>
            );
          })}
        </div>
        {paymentMethods.map((item, index) => {
          return (
            <div
              key={item.id}
              style={{
                padding: 20,
                borderBottom: `1px solid ${theme.colors.lavenderMist}`,
                ...utils.rowCenterSpcBtw(),
                cursor: 'pointer',
              }}
              onClick={() => setSelectedMethod(item)}
            >
              <text.H5>{item.method}</text.H5>
              <div
                style={{
                  width: 20,
                  height: 20,
                  borderRadius: 10,
                  border: '2px solid #DBE3F5',
                  ...utils.flexCenter(),
                }}
              >
                <div
                  style={{
                    width: 10,
                    height: 10,
                    borderRadius: 5,
                    backgroundColor:
                      selectedMethod.id === item.id
                        ? theme.colors.accentColor
                        : theme.colors.transparent,
                  }}
                />
              </div>
            </div>
          );
        })}
      </main>
    );
  };

  return (
    <div id='screen'>
      {renderHeader()}
      {renderContent()}
    </div>
  );
};
