import React from 'react';

import {text} from '../text';
import {utils} from '../utils';
import {svg} from '../assets/svg';
import {theme} from '../constants';
import {PromocodeType} from '../types';

type Props = {
  isLast: boolean;
  item: PromocodeType;
};

export const PromocodeItem: React.FC<Props> = ({item, isLast}) => {
  return (
    <div
      style={{
        cursor: 'pointer',
        ...utils.rowCenter(),
        marginBottom: isLast ? 0 : 20,
        padding: '0px 20px 20px 20px',
        borderBottom: `1px solid ${theme.colors.lavenderMist}`,
      }}
      onClick={() => {
        navigator.clipboard.writeText(item.code);
        alert('Promocode copied to clipboard');
      }}
    >
      <img src={item.image} alt='Promocode' style={{width: 73, height: 84}} />
      <div
        style={{flexDirection: 'column', marginLeft: 14, marginRight: 'auto'}}
      >
        <text.H4>{item.code}</text.H4>
        <span
          style={{
            fontSize: 21,
            ...theme.fonts.Mulish_600SemiBold,
            lineHeight: 1.7,
            color:
              item.discount === 50
                ? '#F4303C'
                : item.discount === 30
                ? '#EF962D'
                : '#00824B',
          }}
        >
          {item.discount}% off
        </span>
        <text.T14>{item.expiry}</text.T14>
      </div>
      <svg.CopySvg />
    </div>
  );
};
