import React, {useState} from 'react';

import {text} from '../text';
import {utils} from '../utils';
import {custom} from '../custom';
import {svg} from '../assets/svg';
import {theme} from '../constants';
import {components} from '../components';

const locations = [
  {
    id: 1,
    name: 'Home',
    address: '8000 S Kirkland Ave, Chicago...',
  },
  {
    id: 2,
    name: 'Work',
    address: '8000 S Kirkland Ave, Chicago...',
  },
  {
    id: 3,
    name: 'Other',
    address: '8000 S Kirkland Ave, Chicago...',
  },
];

export const CheckoutShippingDetails: React.FC = () => {
  const [enteredAddress, setEnteredAddress] = useState(false);
  const [useCurrentLocation, setUseCurrentLocation] = useState(false);
  const [selectedLocation, setSelectedLocation] = useState(locations[0]);

  const renderHeader = (): JSX.Element => {
    return (
      <components.Header title='Shipping details' goBack={true} line={true} />
    );
  };

  const renderContent = (): JSX.Element => {
    return (
      <main style={{padding: '52px 0 20px 0'}}>
        <img
          alt='map'
          src={require('../assets/other/01.jpg')}
          style={{width: '100%', marginBottom: 20}}
        />
        {locations.map((item, index) => {
          return (
            <div
              style={{
                cursor: 'pointer',
                marginBottom: 20,
                ...utils.rowCenterSpcBtw(),
                padding: '0 20px 20px 20px',
                borderBottom: `1px solid #DBE3F5`,
              }}
              onClick={() => setSelectedLocation(item)}
            >
              <div>
                <text.H5 style={{marginBottom: 4}}>{item.name}</text.H5>
                <text.T14>{item.address}</text.T14>
              </div>
              <div
                style={{
                  width: 20,
                  height: 20,
                  borderRadius: 10,
                  border: '2px solid #DBE3F5',
                  ...utils.flexCenter(),
                }}
              >
                <div
                  style={{
                    width: 10,
                    height: 10,
                    borderRadius: 5,
                    backgroundColor:
                      selectedLocation.id === item.id
                        ? theme.colors.accentColor
                        : theme.colors.transparent,
                  }}
                />
              </div>
            </div>
          );
        })}
        <div
          style={{
            padding: '0 20px',
            ...utils.rowCenterSpcBtw(),
            marginBottom: 10,
          }}
        >
          <custom.InputField
            containerStyle={{width: '90%'}}
            label='enter an address'
            placeholder='3646 S 58th Ave, Cicero, IL 608...'
          />
          <div
            style={{
              width: 20,
              height: 20,
              borderRadius: 10,
              border: '2px solid #DBE3F5',
              ...utils.flexCenter(),
              cursor: 'pointer',
            }}
            onClick={() => {
              setEnteredAddress(!enteredAddress);
            }}
          >
            <div
              style={{
                width: 10,
                height: 10,
                borderRadius: 5,
                backgroundColor: enteredAddress
                  ? theme.colors.accentColor
                  : theme.colors.transparent,
              }}
            />
          </div>
        </div>
        <div
          style={{padding: '0 20px', ...utils.rowCenter(), cursor: 'pointer'}}
          onClick={() => {
            setUseCurrentLocation(!useCurrentLocation);
          }}
        >
          <div
            style={{
              width: 18,
              height: 18,
              borderRadius: 5,
              border: '2px solid #E8EFF4',
              ...utils.flexCenter(),
            }}
          >
            {useCurrentLocation && <svg.InputCheckSvg />}
          </div>
          <text.T14 style={{marginLeft: 10}}>Use current location</text.T14>
        </div>
      </main>
    );
  };

  return (
    <div id='screen'>
      {renderHeader()}
      {renderContent()}
    </div>
  );
};
