import React from 'react';

import {text} from '../text';
import {utils} from '../utils';
import {svg} from '../assets/svg';
import {theme} from '../constants';

type Props = {
  title: string;
  titleStyle?: any;
  navIcon?: boolean;
  icon?: JSX.Element;
  onClick?: () => void;
  emailVerify?: boolean;
  goNavigation?: boolean;
  containerStyle?: React.CSSProperties;
};

export const ProfileItem: React.FC<Props> = ({
  titleStyle,
  title,
  icon,
  onClick,
  navIcon = true,
  containerStyle,
}) => {
  return (
    <button
      onClick={onClick}
      style={{
        display: 'block',
        width: '100%',
        ...utils.rowCenter(),
        padding: '10px 20px',
        borderTop: `1px solid ${theme.colors.lavenderMist}`,
        borderRight: 0,
        ...containerStyle,
      }}
    >
      {icon}
      <text.H5
        style={{
          color: theme.colors.mainColor,
          marginLeft: 14,
          ...titleStyle,
        }}
      >
        {title}
      </text.H5>
      {onClick && navIcon && (
        <div style={{marginLeft: 'auto'}}>
          <svg.RightArrowSvg />
        </div>
      )}
    </button>
  );
};
