import React from 'react';

import {text} from '../text';
import {hooks} from '../hooks';
import {svg} from '../assets/svg';
import {components} from '../components';

const addressess = [
  {
    id: 1,
    title: 'Home',
    address: '8000 S Kirkland Ave, Chicago...',
    icon: <svg.AddressHomeSvg />,
  },
  {
    id: 2,
    title: 'Work',
    address: '8000 S Kirkland Ave, Chicago...',
    icon: <svg.BriefcaseSvg />,
  },
  {
    id: 3,
    title: 'Other',
    address: '8000 S Kirkland Ave, Chicago...',
    icon: <svg.AddressMapPinSvg />,
  },
];

export const MyAddress: React.FC = () => {
  const navigate = hooks.useAppNavigate();

  const renderHeader = (): JSX.Element => {
    return <components.Header title='My Address' goBack={true} />;
  };

  const renderContent = (): JSX.Element => {
    return (
      <main style={{padding: '72px 0 20px 0'}}>
        {addressess.map((address, index, array) => {
          const isLast = index === array.length - 1;
          return (
            <div
              style={{
                display: 'flex',
                alignItems: 'center',
                flexDirection: 'row',
                padding: '0 20px',
                borderBottom: '1px solid #DBE3F5',
                paddingBottom: 10,
                marginBottom: isLast ? 30 : 10,
                cursor: 'pointer',
              }}
              onClick={() => {
                navigate('/AddANewAddress');
              }}
            >
              <div>{address.icon}</div>
              <div style={{marginLeft: 14, marginRight: 'auto'}}>
                <text.H5>{address.title}</text.H5>
                <text.T14>{address.address}</text.T14>
              </div>
              <svg.AddressEditSvg />
            </div>
          );
        })}
        <div
          style={{alignItems: 'center', cursor: 'pointer'}}
          onClick={() => {
            navigate('/AddANewAddress');
          }}
        >
          <svg.PlusSvg />
          <text.T14 style={{textAlign: 'center', marginTop: 5}}>
            Add a new address
          </text.T14>
        </div>
      </main>
    );
  };

  return (
    <div id='screen'>
      {renderHeader()}
      {renderContent()}
    </div>
  );
};
