import React from 'react';

import {hooks} from '../hooks';
import {CarouselType, ProductType} from '../types';

type Props = {
  item: CarouselType;
  productsData: ProductType[];
};

export const CarouselItem: React.FC<Props> = ({item, productsData}) => {
  const navigate = hooks.useAppNavigate();
  return (
    <div
      // key={item.id}
      className='embla__slide'
      style={{
        position: 'relative',
        height: 300,
        width: '100%',
        overflow: 'hidden',
        cursor: 'pointer',
      }}
    >
      <img
        src={item.image}
        style={{height: '100%', objectFit: 'cover'}}
        onClick={() => {
          navigate('/shop', {
            state: {products: productsData, title: 'Shop'},
          });
        }}
      />
    </div>
  );
};
